import React, { useState, useEffect } from 'react';
import './Form.css';

function Form({ addProduct }) {
  const [formData, setFormData] = useState({
    productName: '',
    numMonths: 1, // Default to 1 month
    consumption: Array(1).fill(''), // Default to an array with one empty value
    Rm: '',
    Dos: '',
    LT: 0.5,
    PP: 1,
    Si: '',
    So: ''
  });

  const [results, setResults] = useState({
    CA: 0,
    SS: 0,
    Qo: 0
  });

  const [showConfirmation, setShowConfirmation] = useState(false);

  // Disable the scroll wheel on number inputs
  useEffect(() => {
    const disableWheel = (e) => {
      if (e.target.type === "number") {
        e.preventDefault(); // Prevent scrolling for number inputs
      }
    };

    // Add the event listener
    window.addEventListener('wheel', disableWheel, { passive: false });

    // Clean up the event listener
    return () => {
      window.removeEventListener('wheel', disableWheel);
    };
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    if (name === 'numMonths') {
      const newConsumption = Array(Number(value)).fill('');
      setFormData({
        ...formData,
        numMonths: value,
        consumption: newConsumption
      });
    } else {
      setFormData({ ...formData, [name]: value });
    }
  };

  const handleConsumptionChange = (e, index) => {
    const { value } = e.target;
    const newConsumption = [...formData.consumption];
    newConsumption[index] = value;
    setFormData({ ...formData, consumption: newConsumption });
  };

  const calculateResults = () => {
    const { consumption, Rm, Dos, LT, PP, Si, So } = formData;
  
    // Calculate total consumption (CT) from the entered values for each month
    const CT = consumption.reduce((acc, monthConsumption) => acc + parseFloat(monthConsumption || 0), 0);
    let CA = CT / (parseFloat(Rm) - parseFloat(Dos) / 30.5);
    CA = Math.round(CA);
  
    let SS = CA * parseFloat(LT);
    SS = Math.round(SS);
  
    let Qo = CA * (parseFloat(LT) + parseFloat(PP)) + SS - (parseFloat(Si) + parseFloat(So));
    Qo = Math.round(Qo * 1.1);
  
    // Ensure Qo is not negative
    if (Qo < 0) {
      Qo = 0;
    }
  
    return { CA, SS, Qo };
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const { CA, SS, Qo } = calculateResults();
    
    addProduct({
      productName: formData.productName,
      CA,
      SS,
      Qo
    });
  
    window.scrollTo(0, 0);
  
    setFormData({
      productName: '',
      numMonths: 1,
      consumption: Array(1).fill(''),
      Rm: '',
      Dos: '',
      LT: 0.5,
      PP: 1,
      Si: '',
      So: ''
    });
    setResults({ CA: 0, SS: 0, Qo: 0 });

    // Show confirmation message
    setShowConfirmation(true);
    setTimeout(() => setShowConfirmation(false), 2000); // Hide message after 2 seconds
  };

  const clearForm = () => {
    setFormData({
      productName: '',
      numMonths: 1,
      consumption: Array(1).fill(''),
      Rm: '',
      Dos: '',
      LT: 0.5,
      PP: 1,
      Si: '',
      So: ''
    });
    setResults({ CA: 0, SS: 0, Qo: 0 });
    window.scrollTo(0, 0);
  };

  return (
    <div className='form-div'>
      <h2>Enter Product Information</h2>
      {/* Confirmation message */}
      {showConfirmation && (
        <div className="confirmation-message">
          Product added to the table successfully!
        </div>
      )}
      <form onSubmit={handleSubmit}>
        <label>
          Product Name:
          <input type="text" name="productName" value={formData.productName} onChange={handleChange} required />
        </label>
        <br />
        <label>
          Number of Months:
          <select name="numMonths" value={formData.numMonths} onChange={handleChange}>
            {[...Array(12).keys()].map((i) => (
              <option key={i + 1} value={i + 1}>
                {i + 1}
              </option>
            ))}
          </select>
        </label>
        <br />
        
        {/* Dynamically render consumption inputs based on number of months */}
        {Array.from({ length: formData.numMonths }, (_, index) => (
          <div key={index}>
            <label>
              Consumption for Month {index + 1}:
              <input 
                type="number" 
                name={`consumption-${index}`} 
                value={formData.consumption[index]} 
                onChange={(e) => handleConsumptionChange(e, index)} 
                required 
              />
            </label>
            <br />
          </div>
        ))}
        
        <label>
          Rm (Number of Months):
          <input type="number" name="Rm" value={formData.Rm} onChange={handleChange} required />
        </label>
        <br />
        <label>
          Dos (Days Out of Stock):
          <input type="number" name="Dos" value={formData.Dos} onChange={handleChange} required />
        </label>
        <br />
        <label>
          LT (Lead Time):
          <input type="number" name="LT" value={formData.LT} onChange={handleChange} required />
        </label>
        <br />
        <label>
          PP (Procurement Time):
          <input type="number" name="PP" value={formData.PP} onChange={handleChange} required />
        </label>
        <br />
        <label>
          Si (Quantity in Transit):
          <input type="number" name="Si" value={formData.Si} onChange={handleChange} required />
        </label>
        <br />
        <label>
          So (Stock on Hand):
          <input type="number" name="So" value={formData.So} onChange={handleChange} required />
        </label>
        <br />
        <button type="submit">Add Product</button>
        <button type="button" onClick={clearForm}>Clear Form</button>
      </form>
    </div>
  );
}

export default Form;
